// Navigation Styles
// =================


// Small Menu
// ----------

[data-nav-menu='small'] {
  @include position(0 null);
  box-shadow: 0 0 size('half-shim') color('shadow');
  display: flex;
  transform: translatex(-102%);
  transition: transform 0.4s;
  width: 100%;

  &[aria-expanded='true'] {
    transform: translatex(0);
  }

  @include below('nav-small') {
    align-content: flex-start;
    flex-wrap: wrap;
  }

  @include above('nav-small') {
    align-items: stretch;
    flex-direction: column;
    width: auto;
  }

  @include above('page') {
    display: none;
  }
}


// Large Menu
// ----------

[data-nav-menu='large'] {
  align-items: stretch;
  display: flex;
  flex: 0 0 auto;
  flex-direction: column;
  margin-left: -100%;
  transition: margin-left 0.4s;

  &[aria-expanded='true'] {
    margin-left: 0;
  }

  @include below('page') {
    display: none;
  }
}


// Toggles
// -------

[data-nav-toggle='small'] {
  @include above('page') {
    display: none;
  }
}

[data-nav-toggle='large'] {
  @include below('page') {
    display: none;
  }
}

[data-nav-toggle] {
  color: color('theme-dark');
  padding: size('half-shim');

  @include above('page') {
    padding-left: size('shim');
    padding-right:  size('shim');
  }

  &:focus,
  &:hover {
    color: color('action');
    outline: 0;
  }

  &:active {
    color: color('focus');
    outline: 0;
  }
}


// Nav Items
// ---------

[data-sassdoc='nav-item'] {
  list-style: none;
  margin-bottom: size('shim');

  @include below('nav-small') {
    width: 50%;

    &:nth-of-type(2n) {
      padding-right: 1rem;
    }
  }
}


// Nav Titles
// ----------

[data-sassdoc='nav-title'] {
  font-weight: bold;
  margin-bottom: size('shim');
  width: 100%;
}


// Nav Subsections
// ---------------

[data-sassdoc='nav-subsection'] {
  flex: 0 0 auto;
  padding: size('shim') 0;

  &:last-of-type {
    flex: 1 0 auto;
  }

  & + & {
    border-top: 1px solid color('theme-light');
  }
}


// Nav Links
// ---------
/// Navigation items, with inactive and active states.
/// @group style-nav
/// @example html
///   <ul data-nav-menu>
///     <li data-sassdoc="nav-item">
///       <a href="#" data-sassdoc-nav="is-not-active">not active</a>
///     </li>
///     <li data-sassdoc="nav-item">
///       <a href="#" data-sassdoc-nav="is-active">active</a>
///     </li>
///   </ul>
[data-sassdoc-nav] {
  #{$link} {
    color: color('background');
    line-height: 1.1;
    padding-bottom: size('quarter-shim');
    position: relative;
    text-decoration: none;
    transition: color 0.4s;

    // Animated underlines
    @include before('') {
      @include position(null null 0 0);
      background: color('theme-light');
      height: size('nav-underline');
      transform: scale3d(0, 1, 1);
      transform-origin: center left;
      transition: transform 0.4s cubic-bezier(0.22, 0.61, 0.36, 1);
      transition-delay: 0.2s;
      width: 100%;
    }
  }

  #{$focus} {
    @include before('') {
      transform: scale3d(1, 1, 1);
      transition-delay: 0s;
    }
  }

  &[data-sassdoc-nav='is-active'] {
    #{$link},
    #{$focus} {
      @include before('') {
        transform: scale3d(1, 1, 1);
        transition-delay: 0s;
      }
    }
  }
}


// Footer Credit
// -------------

.footer-credit {
  @include clearfix;
  border-top: 1px solid color('theme-light');
  flex: 0 0 auto;
  font-size: size('xsmall');
  line-height: size('rhythm');
  padding-top: size('shim');

  a {
    #{$link} {
      color: color('background');
      font-weight: bold;
      text-decoration-color: transparent;
    }

    #{$focus} {
      text-decoration-color: currentColor;
    }
  }

  [data-icon] {
    float: left;
    margin-right: size('half-shim');
  }

  span {
    display: block;
    overflow: hidden;
    white-space: nowrap;
  }
}
